<template>
  <div class="em-tab">
    <ul class="tab-list">
      <li class="tab-li" v-for="(item, index) in tabItem"  @click="handelSwitch(index)">
        <span class="tab-text" :class="{active: index === actIndex}" v-html="item"></span>
      </li>
    </ul>
    <slot></slot>
  </div>
</template>
<script>
const util = require('@/utils/tool.js')
export default {
  name: 'tab',
  data() {
      return {
        actIndex: this.activedIndex || 0
      }
  },
  props: {
    tabItem:{
      type : Array ,
      default: ()=> ['选项一','选项二','选项三']
    }
  },
	methods: {
    handelSwitch(index){
      this.actIndex=index;
      this.$emit('click', index);
    }
	}
}
</script>
<style lang="scss" scoped>
.em-tab{
  margin-top:8.8rem;
  .tab-list{
    display: inline-block;
    width: 100%;
    .tab-li{
      background: #1C1C1C;
      position: relative;
      z-index: 9;
      width: 25%;
      height: 4rem;
      line-height: 4rem;
      display: inline-block;
      font-size: 1.5rem;
      color: #8E8E93;
      cursor: pointer;
      text-align: center;
      .tab-text{
        height: 3.6rem;
        line-height: 3.6rem;
        padding:0 0.8rem;
        display: inline-block;
        border-bottom: 0.2rem solid #1C1C1C;
        &.active{
          color: #409eff;
          border-color: #409eff;
        }
      }
    }
  }
}

</style>



