<template>
<div class="finance-picker" :class="show?'showpdbtm':''">
   <table class="date-picker">
       <thead>
           <tr class="date-head">
             <th colspan="7">
               <i class="icon-menu icon-left" @click="monthClick(-1)"></i>
               <span class="show-yearmonth">{{curYear}}年{{curMonth}}月(北京时间)</span>
               <i class="icon-menu icon-right" @click="monthClick(1)"></i>
               <span v-if="show" @click="show=false" class="cancel"></span>
               <span v-if="!show" @click="show=true" class="time-icon"></span>
             </th>
           </tr>
           <tr v-if="show" class="date-days">
             <th v-for="(weekname,index) in weekList">{{weekname}}</th>
           </tr>
       </thead>
       <tbody v-show="show" id="touchBox1">
         <tr v-for="i in 6">
           <td v-for="(item, index) in cutdatelist(i,7)" @click="selectDate(item.year,item.month,item.showDate,item.date,index)">
              <span :class="item.classStr">{{item.showDate}}</span>
           </td>
         </tr>
       </tbody>
       <tbody class="picker-oneline" v-show="!show" id="touchBox2">
         <tr class="date-days">
           <th v-for="(weekname,i) in weekList" @click="selectline(i)" :class="(i==lineIndex)&&linedataChosen(value)?'curline-index':''">{{weekname}}</th>
         </tr>
         <tr>
           <td v-for="(item, j) in lineDatalist" :class="[item.classStr,(j==lineIndex)&&linedataChosen(value)?' date-now':'']" @click="selectline(j)"><span :class="[item.classStr,(j==lineIndex)&&linedataChosen(value)?' date-now':'']">{{item.showDate}}</span></td>
         </tr>
       </tbody>
   </table>
</div>
</template>
<script>
const util = require('@/utils/tool.js')
// 现在的时间
const todayDate = new Date(),
    todayYear = todayDate.getFullYear(),
    todayMonth = todayDate.getMonth() + 1,
    todayDay=todayDate.getDate();
      var nowdateYear=todayDate.getFullYear();
export default {
  name: 'finance-picker',
  props: {
    format:{
      type:String,
      default:'-'
    }
  },
  data () {
      return {
          show: false,
          weekList: ['日','一','二','三','四','五','六'],
          datelist:[],
          curYear:new Date().getFullYear(),
          curMonth:new Date().getMonth() + 1,
          formatLine:this.format,
          value:[todayYear,util.numLessTen(todayMonth),util.numLessTen(todayDay)].join(this.format),
          lineIndex:0,
          rowIndex:0,
          lineDatalist:[],
          todayIndex:0
      }
  },
  created(){
    var that=this;
    this.init(this.curYear,this.curMonth);
  },
  mounted(){
    this.lineDatalist=this.lineChosenlist(this.datelist);
    var touchBox1=util.getId('touchBox1');
    var touchBox2=util.getId('touchBox2');
    var that=this,startX, startY;
    var handel1=function(event){
      startX = event.touches[0].pageX;
      startY = event.touches[0].pageY;
    }
    var handel2=function (event) {
      let endX, endY;
      endX = event.changedTouches[0].pageX;
      endY = event.changedTouches[0].pageY;
      let direction = that.GetSlideDirection(startX, startY, endX, endY);
      switch(direction) {
          case 0:
              console.log("没滑动");
              break;
          case 1:
              console.log("向上");
              break;
          case 2:
              console.log("向下");
              break;
          case 3:
              console.log("向左");
              that.show?that.monthClick(1):that.lineClick(1);
              break;
          case 4:
              console.log("向右");
              that.show?that.monthClick(-1):that.lineClick(-1);
              break;
          default:break;
      }
    }
    touchBox1.addEventListener("touchstart",handel1 ,false)
    touchBox1.addEventListener('touchend', handel2,false);
    touchBox2.addEventListener("touchstart",handel1 ,false)
    touchBox2.addEventListener('touchend', handel2,false);
  },
  watch:{
    show(val){
      var tabbody=document.querySelector(".em-tab");
      if(val){
        if(!util.hasClass(tabbody,'layer')){
          util.addClass(tabbody,'layer');
        }
      }
      if(!val){
        util.removeClass(tabbody,'layer');
        this.lineDatalist=this.lineChosenlist(this.datelist);
      }
    }
  },
  beforeUpdate(){
    var that=this;
    var body=document.querySelector(".em-tab");
    body.onclick=function(e){
      e.stopPropagation();
      setTimeout(function(){
        that.show=false;
      },100)
    };
  },
  methods:{
    GetSlideAngle(dx, dy) {
        return Math.atan2(dy, dx) * 180 / Math.PI;
    },
    GetSlideDirection(startX, startY, endX, endY) {
        var dy = startY - endY;
        var dx = endX - startX;
        var result = 0;
        //如果滑动距离太短
        if(Math.abs(dx) < 2 && Math.abs(dy) < 2) {
            return result;
        }
        var angle = this.GetSlideAngle(dx, dy);
        if(angle >= -45 && angle < 45) {
            result = 4;
        }else if (angle >= 45 && angle < 135) {
            result = 1;
        }else if (angle >= -135 && angle < -45) {
            result = 2;
        }
        else if ((angle >= 135 && angle <= 180) || (angle >= -180 && angle < -135)) {
            result = 3;
        }
        return result;
    },
    cutdatelist(i,row){
      return this.datelist.slice((i-1)*row,i*row);
    },
    lineChosenlist(arr){
      var tempArr=this.value.toString().split(this.formatLine);
      for(var kk=0;kk< arr.length;kk++){
        if((tempArr[0]==arr[kk].year)&&(tempArr[1]==arr[kk].month)&&(tempArr[2]==arr[kk].date)){
          this.todayIndex=kk;
          this.rowIndex=Math.floor(kk/7);
          break;
        }
      }
      //this.lineIndex是每一列的判断是星期几（第几列）
      //this.rowIndex是每一行的判断是该日期在日历中的哪一行
      this.lineIndex=this.todayIndex-this.rowIndex*7;//可以判断是星期几
      return arr.slice(this.rowIndex*7,this.rowIndex*7+7);
    },
    //初始化日历
    init(year,month){
      var firstDay ;
      firstDay = new Date(year, month - 1, 1);
      var firstDayWeekDay = firstDay.getDay();
      if (firstDayWeekDay === 0) {
          firstDayWeekDay = 7;
      }
      year = firstDay.getFullYear();
      month = firstDay.getMonth() + 1;
      let day = firstDay.getDate(),
          lastDayOfLastMonth = new Date(year, month - 1, 0),
          lastDateOfLastMonth = lastDayOfLastMonth.getDate(),
          preMonthDayCount = firstDayWeekDay - 1,
          lastDay = new Date(year, month, 0),
          lastDate = lastDay.getDate();
      this.datelist=[];
      for (let i = 0; i < 7 * 6; i++) {
          var date = i  - preMonthDayCount;
          var showDate = date;
          var thisMonth = month;
          var thisYear = year;
          if (date <= 0) {
              thisMonth = month - 1;
              showDate = lastDateOfLastMonth + date;
          } else if (date > lastDate) {
              thisMonth = month + 1;
              showDate = showDate - lastDate;
          }
          if (thisMonth == 0) {
              thisMonth = 12;
              thisYear+=1;
          }
          if (thisMonth == 13) {
              thisMonth = 1;
              thisYear+=1;
          }
          this.datelist.push({
              year:thisYear,
              month: thisMonth,
              date: date,
              showDate: showDate,
              classStr:''
          });
      }
      var tempValueArr=this.value.toString().split(this.formatLine);
      for(let index=0;index< this.datelist.length;index++){
        //上一个月和下一个在本月显示的天数 添加灰色样式
        if(index<=preMonthDayCount || index>(lastDate+preMonthDayCount)){
          this.datelist[index].classStr="prenext-date";
        }
        if(index==14||index==20||index==7){
          this.datelist[index].classStr+=" blue-date";
        }
        if(index==23||index==25){
          this.datelist[index].classStr+=" purple-date";
        }
        if(index==17||index==15||index==16||index==19){
          this.datelist[index].classStr+=" twodot-date";
        }
        if((this.datelist[index].showDate==tempValueArr[2])&&(this.datelist[index].month==tempValueArr[1])&&(this.datelist[index].year==tempValueArr[0])){
          if(this.datelist[index].classStr.indexOf("date-active")==-1){
            this.datelist[index].classStr+=' date-active';
          }
          else{
            this.datelist[index].classStr=this.datelist[index].classStr.replace("date-active","");
          }
        }
      }
    },
    isCurmonthDate(year,month,date){
      if(date<1){
        return -1;
      }
      month--;
      if(month==0){
        month=12;
      }
      console.log(month,date);
      if(month==1||month==3||month==5||month==7||month==8||month==10||month==12){
        if(date>31){
          return 1;
        }
      }
      if(month==4||month==6||month==9||month==11){
        if(date>30){
          return 1;
        }
      }
      if(month==2){
        if((this.isLeapYear(year))&&(date>29)){
          return 1;
        }
        if((!this.isLeapYear(year))&&(date>28)){
          return 1;
        }
      }
      else{
        return 0;
      }
    },
    isLeapYear(year) {
      return (year % 4 == 0) && (year % 100 != 0 || year % 400 == 0);
    },
    selectDate(year,month,day,date,i){
      console.log(this.isCurmonthDate(year,month,date));
      if(this.isCurmonthDate(year,month,date)==-1){
        this.monthClick(-1);
      }
      if(this.isCurmonthDate(year,month,date)==1){
        this.monthClick(1);
      }
      this.lineIndex=i;
      this.value=[year,util.numLessTen(month),util.numLessTen(day)].join(this.formatLine);
      console.log(this.value);
      for(let index=0;index< this.datelist.length;index++){
        if((this.datelist[index].showDate==day)&&(this.datelist[index].month==month)&&(this.datelist[index].year==year)){
          if(this.datelist[index].classStr.indexOf("date-active")==-1){
            this.datelist[index].classStr+=' date-active';
          }
        }
        else{
          this.datelist[index].classStr=this.datelist[index].classStr.replace("date-active","");
        }
      }
      this.show=false;
    },
    selectline(index){
      console.log("点击",index);
      this.lineIndex=index;
      this.value=[this.lineDatalist[index].year,this.lineDatalist[index].month,this.lineDatalist[index].showDate].join(this.formatLine);
      console.log(this.value);
      const [nowyear,nowmonth,nowday]=[this.lineDatalist[index].year,this.lineDatalist[index].month,this.lineDatalist[index].showDate];
      for(let kkk=0;kkk< this.lineDatalist.length;kkk++){
        if(!((this.lineDatalist[kkk].showDate==nowday)&&(this.lineDatalist[kkk].month==nowmonth)&&(this.lineDatalist[kkk].year==nowyear))){
          this.lineDatalist[kkk].classStr=this.lineDatalist[kkk].classStr.replace("date-active","");
        }
      }
    },
    togglePicker(){
      this.show=!this.show;
    },
    linedataChosen(str){
      var tempArr=str.toString().split(this.formatLine);
      for(let item of this.lineDatalist){
        if((item.showDate==tempArr[2])&&(item.month==tempArr[1])&&(item.year==tempArr[0])){
          return true;
        }
      }
    },
    monthClick(flag){
      this.curMonth+=flag;
      if(this.curMonth==0){
        this.curMonth=12;
        this.curYear-=1;
      }
      if(this.curMonth==13){
        this.curMonth=1;
        this.curYear+=1;
      }
      this.init(this.curYear,this.curMonth);
      this.animateClass(flag,util.getId("touchBox1"));
      //切换月份，此时lineDatalist也会做相应的变化
      this.lineDatalist=this.lineChosenlist(this.datelist);
    },
    lineClick(flag){
      this.animateClass(flag,util.getId("touchBox2"));
      if((flag==1)&&(this.rowIndex==4)){
        if(this.curMonth==12){
          console.log("到下一页的日历了");
          this.curMonth=1;
          this.curYear+=1;
        }
        else{
          this.curMonth++;
        }
        this.init(this.curYear,this.curMonth);
        this.rowIndex=0;
        this.lineDatalist=this.datelist.slice(this.rowIndex*7,this.rowIndex*7+7);
        this.removeTouch(this.value);
        return ;
      }
      if((flag==-1)&&(this.rowIndex==0)){
        if(this.curMonth==1){
          console.log("到上一页的日历了");
          this.curMonth=12;
          this.curYear-=1;
        }
        else{
          this.curMonth--;
        }
        this.init(this.curYear,this.curMonth);
        this.rowIndex=5;
        this.lineDatalist=this.datelist.slice(this.rowIndex*7,this.rowIndex*7+7);
        this.removeTouch(this.value);
        return ;
      }
      this.rowIndex+=flag;
      //防止信息 有误
      if(this.rowIndex==6){
        this.rowIndex=0;
      }
      this.lineDatalist=this.datelist.slice(this.rowIndex*7,this.rowIndex*7+7);
      this.removeTouch(this.value);
    },
    removeTouch(valueStr){
      var tempValue=valueStr.toString().split(this.formatLine);
      for(var index=0;index< this.lineDatalist.length;index++){
        if((this.lineDatalist[index].showDate==tempValue[2])&&(this.lineDatalist[index].month==tempValue[1])&&(this.lineDatalist[index].year==tempValue[0])){
          if(this.lineDatalist[index].classStr.indexOf("date-active")==-1){
            this.lineDatalist[index].classStr+=' date-active';
          }
        }
        else{
          this.lineDatalist[index].classStr=this.lineDatalist[index].classStr.replace("date-active","");
        }
      }
    },
    animateClass(flag,obj){
      if(flag==1){
        if(!util.hasClass(obj,"picker-animate2")){
          util.addClass(obj,"picker-animate2");
          setTimeout(
            function(){
              util.removeClass(obj,"picker-animate2");
          }, 200 )
        }
      }
      else{
        if(!util.hasClass(obj,"picker-animate1")){
          util.addClass(obj,"picker-animate1");
          setTimeout(
            function(){
              util.removeClass(obj,"picker-animate1");
          }, 200 )
        }
      }
    }
  }
}
</script>
<style lang="scss" scoped>
@mixin border_vertical_half($color: $borderColor){
  position: relative;
  &:after{
    content: '';
    width: 200%;
    height: 200%;
    position: absolute;
    top: 0;
    left: 0;
    box-sizing: border-box;
    border-bottom: 1px solid $color;
    border-top: 1px solid $color;
    -webkit-transform: scale(0.5,0.5);
    transform: scale(0.5,0.5);
    transform-origin: top left;
    -webkit-transform-origin: top left;
  }
}
@keyframes animateleft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes animateright {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

.finance-picker {
  position: relative;
  display: inline-block;
  color: #333;
  box-sizing: border-box;
  width: 100%;
  height: auto;
  background: #1c1c1c;
  font-size: 1.5rem;
  font-weight: bold;
  z-index: 99;
  &.showpdbtm{
    padding-bottom:0.8rem;
  }
  tbody{
    &.picker-animate1{
      animation: animateleft 0.2s cubic-bezier(.645,.045,.355,1);;
    }
    &.picker-animate2{
      animation: animateright 0.2s cubic-bezier(.645,.045,.355,1);;
    }
  }
  .date-picker {
      width: 100%;
      border-collapse: collapse;
      border-spacing: 0;
      text-align: center;
      color:#fff;
      z-index: 0;
      tr {
        width: 100%;
          height: 3.6rem;
          &.date-head {
            text-align: center;
            color: #fff;
            th{
              width: 100%;
              display: block;
              @include border_vertical_half(#0D0D0D);
              margin:0;
              height: 3.6rem;
              line-height: 3.6rem;
              position:relative;
              .cancel{
                position: absolute;
                right:2rem;
                top:1.2rem;
                display: inline-block;
                width: 1.2rem;
                height: 1.2rem;
                z-index: 9;
                background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAABgAAAAYBAMAAAASWSDLAAAAElBMVEUAAAAwdMIwdMIwdMIwdMIwdMIX+wD4AAAABXRSTlMA1BQGsO1JN9kAAABaSURBVBjTVZDBCcAwCEWl3SDQe7JBD92gE2T/ZQKf5+EJUfQF9Vv3LOx66xudrL/WnoBn5MVSjQvAAwiUEqmAGgQFNApoBHDib25Aaw/VOl5UEiROsnUQneoAtNUP8WHoSjMAAAAASUVORK5CYII=) no-repeat;
                background-size: 100%;
              }
              .time-icon{
                position: absolute;
                right:1.6rem;
                top:0.9rem;
                width: 1.8rem;
                height: 1.8rem;
                display: inline-block;
                background: url(data:image/png;base64,) top center no-repeat;
                background-size: 100%;
                z-index:9;
              }
            }
            .icon-menu{
              display: inline-block;
              width: 0.7rem;
              height: 0.7rem;
              border: solid #3274C2;
              border-width: 0.1rem 0.1rem 0 0;
              position:relative;
              z-index: 2;
              &.icon-left{
                margin-right: 1.2rem;
                -webkit-transform:rotate(225deg);
                transform:rotate(225deg);
              }
              &.icon-right{
                margin-left: 1.2rem;
                -webkit-transform:rotate(45deg);
                transform:rotate(45deg);
              }
            }
          }
          &.date-days {
            color:#6C6C6C;
            font-weight: 400;
            position:relative;
            z-index: 2;
            th{
              display: inline-block;
            }
            .curline-index{
              background: #3274C2;
              color:#fff;
              border-radius: 0;
            }
          }
      }
      th,td{
        margin:0 2%;
        user-select: none;
        width:10.285%;
        height:3.2rem;
        border: 0 none;
        line-height:3.2rem;
        text-align: center;
        border-radius: 50%;
        display: inline-block;
        position: relative;
        z-index: 1;
      }
      th{
        display: inline;
        .show-yearmonth {
          display: inline-block;
          min-width: 12rem;
          cursor: pointer;
        }
      }
      td {
        cursor: pointer;
        position: relative;
        color:#fff;
        .date-active {
          display: inline-block;
          height: 2.6rem;
          line-height: 2.6rem;
          width: 2.6rem;
          color: #fff;
          background-color: #3274C2;
          border-radius: 50%;
          vertical-align: middle;
        }
        span{
          display: inline-block;
          height: 2.6rem;
          width: 2.6rem;
          &.prenext-date{
            color:#6C6C6C;
          }
        }
        .blue-date{
          &:after{
            content: '';
            position: absolute;
            bottom: -0.5rem;
            left:0;
            right: 0;
            margin:0 auto;
            height: 0.5rem;
            width: 0.5rem;
            border-radius: 50%;
            background-color: #3b86e2;
          }
        }
        .purple-date{
          &:after{
            content: '';
            position: absolute;
            bottom: -0.5rem;
            left:0;
            right: 0;
            margin:0 auto;
            height: 0.5rem;
            width: 0.5rem;
            border-radius: 50%;
            background-color: #9C52A9;
          }
        }
        .twodot-date{
          &:before{
            content: '';
            position: absolute;
            bottom: -0.5rem;
            left:0.8rem;
            height: 0.5rem;
            width: 0.5rem;
            border-radius: 50%;
            background-color: #3b86e2;
          }
          &:after{
            content: '';
            position: absolute;
            bottom: -0.5rem;
            right:0.8rem;
            height: 0.5rem;
            width: 0.5rem;
            border-radius: 50%;
            background-color: #9C52A9;
          }
        }
      }
      .picker-oneline{
        background: #272727;
        tr{
          height: 2rem;
          th{
            height: 2.6rem;
            line-height:2.6rem;
          }
          td{
            line-height: 1;
            height: 2.6rem;
            font-size: 1.5rem;
            &.date-now{
              color: #fff;
              background-color: #3274C2;
              border-radius: 0;
            }
            .date-active{
              border-radius: 0;
              height: 2.6rem;
              line-height:1;
            }
            .blue-date{
              &:after{
                content: '';
                bottom: 0.4rem;
              }
            }
            .purple-date{
              &:after{
                content: '';
                bottom: 0.4rem;
              }
            }
            .twodot-date{
              &:before{
                content: '';
                bottom: 0.4rem;
              }
              &:after{
                content: '';
                bottom: 0.4rem;
              }
            }
          }
        }
      }

  }
}
</style>



